div#simple_popup {
  width: 100%;
  height: 100%;
  // padding-top:90px;
  // padding-bottom:100px;

  top: 85px;
  bottom: 100px;
  overflow: visible;
  background: #eee;
  position: absolute;
  bottom: 0;
  z-index: 3;

  .container {
    max-width: 900px;
    //height: 100%;
  }

  .content {
    overflow: scroll;
    height: 100%;
    padding-bottom: 150px;
    width: 100%;
  }

  h3 {
    @include font-size(33px);
    font-weight: 400;
    margin: 50px 0 50px 0;
    text-transform: uppercase;
  }

  p {
    margin-bottom: 20px !important;
    font-weight: 300;
    line-height: 1.4em;
    padding-right: 40px;

    &.disclaimer {
      font-size: 0.9em;
      margin-top: 20px;
    }
  }

  a {
    font-style: normal;
  }

  a.close {
    position: absolute;
    top: 0;
    right: 0px;
    color: $colorSecondary;
    display: block;
    @include font-size(37px);

    &:hover {
      opacity: 0.7;
      transform: rotate(180deg);
    }
  }
}

#contact_form {
  .input_holder {
    width: 50%;
    //color:#959595;
    margin-bottom: 25px;

    &.message {
      width: 100%;
      margin-bottom: 0;
    }

    label {
      @include font-size(16px);
      display: block;
      margin-bottom: 11px;
      width: 100%;
      font-weight: 300;

      span.required {
        display: none;
      }
    }

    .input {
      @include textInput(90%);
      border: 1px solid #ebe6f1;
    }
    .input.password {
      @include textInput(90%);
      border: 1px solid #ebe6f1;
    }
    .input.textarea {
      @include textInput(95%);
      border: 1px solid #ebe6f1;
      height: 100px;
      line-height: 1.5em;
      padding-top: 10px;
    }

    &.select_option select {
      border-radius: 7px;
      border: none;
      padding: 0 18px;
      height: 46px;
      background: #fff;
      border: 1px solid #ebe6f1;
      box-shadow: 0px 4px 0px 0px #ebe6f1;
      color: #888888;
      font-style: italic;
      @include font-size(18px);
      font-weight: 300;
      width: 90%;
    }
    &.error {
      label {
        .required {
          display: inline;
          color: $colorSecondary;
        }
      }
      .input {
        @include textInput(90%);
        border: 1px solid $colorSecondary;
        color: $colorSecondary;
      }
      .input.textarea {
        @include textInput(95%);
        border: 1px solid $colorSecondary;
        height: 100px;
        line-height: 1.5em;
        padding-top: 10px;
      }
      ::-webkit-input-placeholder {
        /* WebKit, Blink, Edge */
        color: $colorSecondary;
      }
      :-moz-placeholder {
        /* Mozilla Firefox 4 to 18 */
        color: $colorSecondary;
        opacity: 1;
      }
      ::-moz-placeholder {
        /* Mozilla Firefox 19+ */
        color: $colorSecondary;
        opacity: 1;
      }
      :-ms-input-placeholder {
        /* Internet Explorer 10-11 */
        color: $colorSecondary;
      }
      :placeholder-shown {
        /* Standard (https://drafts.csswg.org/selectors-4/#placeholder) */
        color: $colorSecondary;
      }
    }

    &.select_option.error select {
      border: 1px solid $colorSecondary;
      //box-shadow:0px 4px 0px 0px $colorSecondary;
      color: $colorSecondary;
    }
  }

  .button {
    @include button(
      179px,
      50px,
      4px,
      $colorSecondary,
      $colorSecondaryDark,
      #fff
    );
    font-family: $hdrFont;
    @include font-size(16px);
    margin-top: 24px;
  }
}
